import React, { Fragment } from 'react';
import Media from 'react-media';
import style from './eighth-screen.module.css';
import Header from './header/header';
import NavigationServices from '../navigationServices/navigationServices';
import Information from './infornation/information';
import imgHead from '../../img/infoImg/head.png';
import imgHeadW from '../../img/infoImg/head.webp';
import eighthScreenSomethinImg from '../../img/background/eighthScreenSomethingDesk.png';
import eighthScreenSomethinImgW from '../../img/background/eighthScreenSomethingDesk.webp';

const SixthSection = ({ onOpenModal }) => (
  <section id="Head" name="Head" className={style.section}>
    <Header />
    <Media query={{ minWidth: 1200 }}>
      {matches =>
        matches && (
          <div className={style.navigation}>
            <NavigationServices number={7} />
          </div>
        )
      }
    </Media>
    <Information onOpenModal={onOpenModal} />
    <Media query={{ minWidth: 1200 }}>
      {matches =>
        matches && (
          <Fragment>
            <picture>
              <source srcSet={imgHeadW} type="image/webp" />
              <source srcSet={imgHead} type="image/png" />
              <img className={style.img} src={imgHead} alt="Head" />
            </picture>
            <picture>
              <source srcSet={eighthScreenSomethinImgW} type="image/webp" />
              <source srcSet={eighthScreenSomethinImg} type="image/png" />
              <img
                className={style.something}
                src={eighthScreenSomethinImg}
                alt="Back"
              />
            </picture>
          </Fragment>
        )
      }
    </Media>
  </section>
);

export default SixthSection;
